      margin: 0 auto;
      // border-style: dashed;
      max-width: 500px;
  
      .vacancies-block-main-card-top {
        padding-top: 100px;
        .card-item::before{
          position: absolute;
          content: "";
          background:  rgba(228, 23, 211, 0.60);
          height: 339px;
          width: 343px;
          left: 18px;
          right: 0;
          top: 130px;
          bottom: 0;
          box-shadow: inset 0 0 0 3000px rgb(150 150 150 / 19%);
          filter: blur(10px);
          border-radius: 15px;
        }
        .card-item {
          transition: 1s;
          cursor: pointer;
          z-index: 9999;
          width: 383px;
          height: 400px;
          border-radius: 15px;
          background: rgba(255, 255, 255, 0.192);
          .card-item-img {
            display: flex;
            justify-content: center;
            padding-top: 115px;
            img {
              z-index: 1;
            }
          }
        }
        .card-item:hover {
