          margin-block-end: 10px;
          margin-inline-start: 0px;
          margin-inline-end: 0px;
        }
      }
      .vacancies-block-main-info {
        gap: 10px;
        display: flex;
        justify-content: start;
        width: 50%;
        height: 40px;
        .vacancies-block-main-info-line {
          align-items: end;
          display: flex;
          padding-bottom: 3px;
        }
        .vacancies-block-main-info-button {
          color: white;
          cursor: pointer;
          display: contents;
          p {
            transition: 1s;
            font-size: 25px;
            font-family: "Montserrat", system-ui;
            display: flex;
            align-items: center;
            text-align: center;
            margin-block-start: 0px;
            margin-block-end: 0px;
          }
          p:hover {
            color: rgb(228, 139, 208);
            text-shadow: rgba(227, 0, 181, 0.6);
            transition: all ease 1s;
          }
          p:active {
            transform: translateY(-1px);
            color: rgba(227, 0, 181, 0.6);
            box-shadow: 0 5px 10px rgba(112, 4, 89, 0.2);
          }
        }
      }
    }
  }
